* {
  margin: 0;
  padding: 0;
}


/*Field START*/

body {
  background-color: #474448;
}

.field__score {
    margin: 20px auto;

    text-align: center;
    font-size: 20px;
    font-family: serif;

    color: #f1f0ea;
}

.field__wrapper {
    background-color: #f1f0ea;

    margin: 20px auto;

    position: relative;
}

.field_block {
    background-color: #d88c9a;

    position: absolute;
    top: 0;
    left: 0;

    border: 1px solid #8e7dbe;
    border-radius: 1px;
}

/*Field END*/